import React, { Fragment, useEffect, useRef } from "react";
import Laptop from "../../img/laptop2.png";
import { TimelineLite, Power3 } from "gsap";

function About() {
  let title = useRef(null);
  let text = useRef(null);
  let laptop = useRef(null);
  let squareBG = useRef(null);

  let tl = new TimelineLite();

  useEffect(() => {
    tl.to(title, 1, { opacity: 1, y: "0%", ease: Power3.easeIn }, "a")
      .to(text, 1, { opacity: 1, y: "0%", ease: Power3.easeIn }, "a")
      .to(laptop, 1, { opacity: 1, y: "-50%", ease: Power3.easeIn }, "a")
      .to(squareBG, 1, { opacity: 1, x: "10%", ease: Power3.easeIn }, "a");

    // eslint-disable-next-line
  }, []);

  return (
    <Fragment>
      <div className="about-page">
        <h1 className="heading white uppercase" ref={(e) => (title = e)}>
          Hi, Iâ€™m Alfer Jay. <br />
          Nice to meet you!
        </h1>
        <p className="text color-white" ref={(e) => (text = e)}>
          With my strong professional background in Web Design and Development,
          I can assure to bring you the best wesbite.
        </p>
      </div>
      <img
        className="laptop"
        src={Laptop}
        alt="Laptop Background"
        ref={(e) => (laptop = e)}
      />
      <div className="home-side-div-container">
        <div className="triangle" ref={(e) => (squareBG = e)}></div>
      </div>
    </Fragment>
  );
}

export default About;
