import React, { useEffect, useRef } from "react";
import { FaLinkedinIn } from "react-icons/fa";
import { MdEmail } from "react-icons/md";
import { GoMarkGithub } from "react-icons/go";
import { ReactComponent as Logo } from "../../img/aj-logo.svg";
import { Link } from "react-router-dom";
import { TweenLite, Power3 } from "gsap";

const footerLinks = [
  { href: "/" },
  { href: "/about" },
  { href: "/skills" },
  { href: "/projects" },
  { href: "/blog" },
  { href: "/contact" },
];

function Footer({ activeLink, handleActiveLink }) {
  let siteFooter = useRef(null);

  useEffect(() => {
    TweenLite.to(siteFooter, 1, {
      delay: 1,
      opacity: 1,
      y: 0,
      ease: Power3.easeInOut,
    });
    // eslint-disable-next-line
  }, []);

  return (
    <footer className="site-footer-wrapper" ref={(e) => (siteFooter = e)}>
      <div className="container">
        <div className="site-footer">
          <Link to="/" onClick={() => handleActiveLink("/")}>
            <Logo className="site-logo-svg" />
          </Link>
          <div className="bullet-container">
            {footerLinks.map((link, index) => (
              <Link
                key={index}
                to={link.href}
                className={`bullet ${activeLink === link.href ? "active" : ""}`}
                onClick={() => handleActiveLink(link.href)}
              ></Link>
            ))}
          </div>
          <ul className="social-links">
            <li>
              <a href="https://linkedin.com/in/alferjay">
                <FaLinkedinIn className="social-icon" />
              </a>
            </li>
            <li>
              <a href="mailto:alferjaysalvador@gmail.com">
                <MdEmail className="social-icon" />
              </a>
            </li>
            <li>
              <a href="https://github.com/alferjay">
                <GoMarkGithub className="social-icon" />
              </a>
            </li>
          </ul>
        </div>
      </div>
    </footer>
  );
}

export default Footer;
