import React, { useRef } from "react";

function TextArea({
  name,
  id,
  children,
  value,
  handleChange,
  rows,
  cols = 50,
}) {
  let label = useRef(null);
  let div = useRef(null);

  const handleFocus = () => {
    value === "" && label.classList.add("active");
    div.classList.add("active");
  };

  const handleBlur = () => {
    value === "" && label.classList.remove("active");
    div.classList.remove("active");
  };

  return (
    <div className="txtbox-container for-text-area" ref={(e) => (div = e)}>
      <label className="for-text-area" htmlFor="name" ref={(e) => (label = e)}>
        {children}
      </label>
      <textarea
        name={name}
        id={id}
        cols={cols}
        value={value}
        onChange={(e) => handleChange(e)}
        onFocus={() => handleFocus()}
        onBlur={() => handleBlur()}
        rows={rows}
      ></textarea>
    </div>
  );
}

export default TextArea;
