import React, { useRef, useEffect } from "react";
import { MdBorderColor, MdDesktopWindows, MdCode } from "react-icons/md";
import { TweenLite, Power3 } from "gsap";

const uiSkills = ["Sketch", "Photoshop", "Illustrator", "After Effects"];
const frontSkills = [
  "HTML5",
  "Javascript ES6",
  "CSS3",
  "SASS",
  "Gulp",
  "React",
];
const backSkills = ["PHP", "Wordpress", "Node", "Express", "MySQL", "MongoDB"];

function Skills() {
  let designer = useRef(null);
  let frontEnd = useRef(null);
  let backEnd = useRef(null);

  useEffect(() => {
    TweenLite.staggerTo(
      [designer, frontEnd, backEnd],
      0.5,
      { opacity: 1, y: "0%", ease: Power3.easeIn },
      0.3
    );
    // eslint-disable-next-line
  }, []);

  return (
    <div className="skills">
      <div className="wrapper">
        <div className="ui" ref={(e) => (designer = e)}>
          <MdBorderColor className="icon" />
          <h2>Designer</h2>
          <p>
            I value simple content structure, clean design patterns, and
            thoughtful interactions.
          </p>
          <p className="list-title"> Design Tools </p>
          <ul>
            {uiSkills.map((skill) => (
              <li key={skill}>{skill}</li>
            ))}
          </ul>
        </div>
        <div className="front-end" ref={(e) => (frontEnd = e)}>
          <MdDesktopWindows className="icon" />
          <h2>Front-End</h2>
          <p>
            I like to code things from scratch, and enjoy bringing ideas to life
            in the browser.
          </p>
          <p className="list-title"> Design Tools </p>
          <ul>
            {frontSkills.map((skill) => (
              <li key={skill}>{skill}</li>
            ))}
          </ul>
        </div>
        <div className="backend" ref={(e) => (backEnd = e)}>
          <MdCode className="icon" />
          <h2>Back-End</h2>
          <p>
            I genuinely care about people, and love helping fellow designers
            work on their craft.
          </p>
          <p className="list-title"> Design Tools </p>
          <ul>
            {backSkills.map((skill) => (
              <li key={skill}>{skill}</li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Skills;
