import React, { useState, useEffect } from "react";
import "./App.scss";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import HomePage from "./components/home/HomePage";
import NavBar from "./components/layout/NavBar";
import About from "./components/about/About";
import Footer from "./components/layout/Footer";
import Contact from "./components/contact/Contact";
import Skills from "./components/skills/Skills";
import Projects from "./components/projects/Projects";
import Blog from "./components/blog/Blog";
import BackgoundImage from "./components/utils/BackgoundImage";
import SplashScreen from "./components/utils/Splash";
import { setInit } from "./components/actions/init";

//redux
import { Provider } from "react-redux";
import store from "./store";

//setInit();

function App() {
  const [activeLink, setActiveLink] = useState(null);

  const handleActiveLink = (link) => {
    setActiveLink(link);
  };

  useEffect(() => {
    store.dispatch(setInit());
  }, []);

  return (
    <Provider store={store}>
      <Router>
        <SplashScreen />
        <NavBar handleActiveLink={handleActiveLink} activeLink={activeLink} />
        <div className="container">
          <Switch>
            <Route
              exact
              path="/"
              render={() => <HomePage handleActiveLink={handleActiveLink} />}
            />
            <Route exact path="/about" component={About} />
            <Route exact path="/contact" component={Contact} />
            <Route exact path="/skills" component={Skills} />
            <Route exact path="/projects" component={Projects} />
            <Route exact path="/blog" component={Blog} />
          </Switch>
        </div>
        <BackgoundImage />
        <Footer handleActiveLink={handleActiveLink} activeLink={activeLink} />
      </Router>
    </Provider>
  );
}

export default App;
