import React, { Fragment, useEffect, useRef } from "react";
import { Link } from "react-router-dom";
import logo from "../../img/aj-logo.svg";
import { TimelineLite, Power3 } from "gsap";
import { connect } from "react-redux";
import PropTypes from "prop-types";

function HomePage({ handleActiveLink, init: { loading } }) {
  let h1 = useRef(null);
  let p = useRef(null);
  let button = useRef(null);
  let myLogo = useRef(null);
  let myLogo1 = useRef(null);
  let tl = new TimelineLite();

  useEffect(() => {
    tl.to(
      h1,
      1,
      { opacity: 1, y: 0, ease: Power3.easeIn },
      //"text"
      !loading ? "+=1" : "text"
    )
      .to(
        myLogo1,
        1,
        { opacity: 1, scale: 1, y: 0, ease: Power3.easeIn },
        "-=1"
      )
      .to(p, 1, { opacity: 1, y: 0, ease: Power3.easeIn }, "-=1")
      .to(button, 1, { opacity: 1, y: 0, ease: Power3.easeIn }, "-=1")
      .to(
        myLogo,
        1.5,
        {
          //y: "-50%",
          opacity: 1,
          scale: 1,
          ease: Power3.easeInOut,
        },
        "-=1"
      );

    // eslint-disable-next-line
  }, []);

  return (
    <Fragment>
      <div className="homepage">
        <div className="wrapper">
          <img
            src={logo}
            className="logo"
            alt="logo"
            ref={(e) => (myLogo1 = e)}
          />
          <h1 className="heading white uppercase" ref={(e) => (h1 = e)}>
            Front-end Developer
            <br />
            Web Designer / Developer
          </h1>
          <p className="text color-white" ref={(e) => (p = e)}>
            Specializing in custom theme development. If you're a business
            seeking a web presence or are looking to hire, contact me here.
          </p>

          <div className="btn-container" ref={(e) => (button = e)}>
            <Link
              className="btn outlined"
              to="/contact"
              onClick={() => handleActiveLink("/contact")}
            >
              Get Started
            </Link>
          </div>
        </div>
      </div>
      <div className="container-wrapper">
        <div className="my-logo-container" ref={(e) => (myLogo = e)}>
          <img src={logo} className="logo" alt="logo" />
        </div>
      </div>
    </Fragment>
  );
}

HomePage.propTypes = {
  handleActiveLink: PropTypes.func,
};

const mapStateToProps = (state) => ({
  init: state.init,
});

export default connect(mapStateToProps, {})(HomePage);
