import React, { useState, Fragment, useRef, useEffect } from "react";
import { TextBox, Button, TextArea } from "../ui";
import { TweenLite, Power4 } from "gsap";

function Contact() {
  const [formValue, setFormValue] = useState({
    name: "",
    email: "",
    subject: "",
    message: "",
  });

  const handleChange = (e) => {
    setFormValue({ ...formValue, [e.target.name]: e.target.value });
  };

  const { name, email, subject, message } = formValue;

  let form = useRef(null);

  useEffect(() => {
    TweenLite.to(form, 0.7, { opacity: 1, y: 0, ease: Power4.easeInOut });
  });

  return (
    <Fragment>
      <div className="contact">
        <div className="form-container" ref={(e) => (form = e)}>
          <h1 className="heading">Let's do this</h1>
          <form action="">
            <TextBox
              type="text"
              name="name"
              id="name"
              value={name}
              handleChange={handleChange}
            >
              Name
            </TextBox>
            <TextBox
              type="email"
              name="email"
              id="email"
              value={email}
              handleChange={handleChange}
            >
              Email
            </TextBox>
            <TextBox
              type="text"
              name="subject"
              id="subject"
              value={subject}
              handleChange={handleChange}
            >
              Subject
            </TextBox>

            <TextArea
              rows="5"
              cols="50"
              name="message"
              id="message"
              value={message}
              handleChange={handleChange}
            >
              Message
            </TextArea>
            <Button style={{ marginLeft: "auto" }}>Submit</Button>
          </form>
        </div>
      </div>
    </Fragment>
  );
}

export default Contact;
