import React, { useRef } from "react";

function TextBox({ name, id, children, type, value, handleChange }) {
  let label = useRef(null);
  let div = useRef(null);

  const handleFocus = () => {
    value === "" && label.classList.add("active");
    div.classList.add("active");
  };

  const handleBlur = () => {
    value === "" && label.classList.remove("active");
    div.classList.remove("active");
  };

  return (
    <div className="txtbox-container" ref={(e) => (div = e)}>
      <label htmlFor="name" ref={(e) => (label = e)}>
        {children}
      </label>
      <input
        type={type}
        name={name}
        id={id}
        value={value}
        onChange={(e) => handleChange(e)}
        onFocus={() => handleFocus()}
        onBlur={() => handleBlur()}
      />
    </div>
  );
}

export default TextBox;
