import React, { useEffect, useRef } from "react";
import { Link, useLocation } from "react-router-dom";
import {
  MdHome,
  MdAccountCircle,
  MdStar,
  MdAssignmentTurnedIn,
  MdReceipt,
  MdMenu,
  MdForum,
} from "react-icons/md";
import { FaLinkedinIn } from "react-icons/fa";
import { MdEmail } from "react-icons/md";
import { GoMarkGithub } from "react-icons/go";

import { TweenLite, Power3 } from "gsap";

const navLinks = [
  {
    id: 1,
    name: "home",
    icon: <MdHome className="navlinks-icon" />,
    href: "/",
  },
  {
    id: 2,
    name: "about",
    icon: <MdAccountCircle className="navlinks-icon" />,
    href: "/about",
  },
  {
    id: 3,
    name: "skills",
    icon: <MdStar className="navlinks-icon" />,
    href: "/skills",
  },
  {
    id: 4,
    name: "projects",
    icon: <MdAssignmentTurnedIn className="navlinks-icon" />,
    href: "/projects",
  },
  {
    id: 5,
    name: "blog",
    icon: <MdReceipt className="navlinks-icon" />,
    href: "/blog",
  },
];

function NavBar({ activeLink, handleActiveLink }) {
  let location = useLocation();
  const curLocation = location.pathname;
  let siteHeader = useRef(null);
  let navLinksMob = useRef(null);
  let closeNav = useRef(null);

  useEffect(() => {
    handleActiveLink(curLocation);
    TweenLite.to(siteHeader, 1, {
      delay: 1,
      opacity: 1,
      y: 0,
      ease: Power3.easeInOut,
    });
    // eslint-disable-next-line
  }, []);

  const openNavLinkMob = () => {
    TweenLite.to(navLinksMob, 1, { x: 0, ease: Power3.easeInOut });
    TweenLite.to(closeNav, 1, { x: 0, ease: Power3.easeInOut });
  };
  const closeNavLinksMob = () => {
    TweenLite.to(navLinksMob, 1, { x: "-100%", ease: Power3.easeInOut });
    TweenLite.to(closeNav, 1, { x: "-100%", ease: Power3.easeInOut });
  };

  return (
    <header ref={(e) => (siteHeader = e)}>
      <div className="container">
        <nav className="nav-bar">
          <div className="mobile-menu">
            <MdMenu
              className="hamburger"
              onClick={openNavLinkMob}
              onBlur={closeNavLinksMob}
            />
          </div>
          <Link
            to="/"
            className="site-logo-text"
            onClick={() => handleActiveLink("/")}
          >
            <h1>AlferJay</h1>
          </Link>
          {/* Mobile NavLinks */}
          <div
            className="close-nav-links-mobile"
            ref={(e) => (closeNav = e)}
            onClick={closeNavLinksMob}
          ></div>
          <ul className="nav-links-mobile" ref={(e) => (navLinksMob = e)}>
            {navLinks.map((link) => (
              <li key={link.id}>
                <Link
                  to={link.href}
                  className={`${activeLink === link.href ? "active" : ""}`}
                  onClick={() => {
                    handleActiveLink(link.href);
                    closeNavLinksMob();
                  }}
                >
                  {link.icon}
                  {link.name}
                </Link>
              </li>
            ))}
            <li className="social-link-container">
              <ul className="social-links">
                <li>
                  <a href="https://linkedin.com/in/alferjay">
                    <FaLinkedinIn className="social-icon" />
                  </a>
                </li>
                <li>
                  <a href="mailto:alferjaysalvador@gmail.com">
                    <MdEmail className="social-icon" />
                  </a>
                </li>
                <li>
                  <a href="https://github.com/alferjay">
                    <GoMarkGithub className="social-icon" />
                  </a>
                </li>
              </ul>
            </li>
          </ul>
          {/* End of Mobile Navlinks */}

          <ul className="nav-links">
            {navLinks.map((link) => (
              <li key={link.id}>
                <Link
                  to={link.href}
                  className={`${activeLink === link.href ? "active" : ""}`}
                  onClick={() => handleActiveLink(link.href)}
                >
                  {link.icon}
                  {link.name}
                </Link>
              </li>
            ))}
          </ul>

          <Link
            to="/contact"
            className={`contact-btn ${
              activeLink === "/contact" ? "active" : ""
            }`}
            onClick={() => handleActiveLink("/contact")}
          >
            Contact Me
          </Link>

          <Link
            to="/contact"
            className="mobile-contact"
            onClick={() => handleActiveLink("/contact")}
          >
            <MdForum className="icon" />
          </Link>
        </nav>
      </div>
    </header>
  );
}

export default NavBar;
