import React, { useRef, useEffect } from "react";
import img1 from "../../img/projects/img1.jpg";
import img2 from "../../img/projects/img2.jpg";
import img3 from "../../img/projects/img3.jpg";
import img4 from "../../img/projects/img4.jpg";
import { TweenLite, Power4 } from "gsap";

const projectList = [
  {
    id: 1,
    bg: img1,
    title: "EuroMaTech Training & Management Consultancy",
    link: "http://www.euromatech.com",
  },
  {
    id: 2,
    bg: img2,
    title: "360Leaders Academy",
    link: "http://the360leaders.com",
  },
  {
    id: 3,
    bg: img3,
    title: "Anderon Professional Certification",
    link: "http://apc.training",
  },
  { id: 4, bg: img4, title: "Copex Traning", link: "http://dev.copex.ae" },
];

function Projects() {
  let grid = useRef(null);

  useEffect(() => {
    let newGrid = Array.from(grid.children);
    newGrid.forEach((item) => {
      TweenLite.to(item, 1, { scale: 1, opacity: 1, ease: Power4.easeInOut });
    });
  }, []);

  return (
    <div className="projects">
      <div className="grid" ref={(e) => (grid = e)}>
        {projectList.map((project) => (
          <div
            className="grid-item"
            style={{ backgroundImage: `url(${project.bg})` }}
            key={project.id}
          >
            <div className="details">
              <h3>{project.title}</h3>
              <a
                href={project.link}
                rel="no-follow"
                className="btn outlined small"
              >
                Visit Website
              </a>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Projects;
